body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main__section{
  background-image: url(/static/media/background.9a7f5544.jpg);
  background-size:100%;
  position: absolute;
  width: 100%;
  height: 92.5%;
}

.main__section img{
  width: 100%;
  height: 116%;
}

.main__section-item{
  background-color: rgba(72, 87, 255, 0.582);
  width: 100%;
  height: 100%;
}

.main__section-item img {
  width: 186px;
  height: 218px;
  margin-left: 21%;
  margin-top: 8%;
  -webkit-transform: translateY(25px);
          transform: translateY(25px);
}

.main__section-item h2{
  font-size: 46pt;
  color: white;
  display: inline;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif
}

.main__section-item p{
  font-size: 20pt;
  color: white;
  font-family:Arial, Helvetica, sans-serif;
  margin-left: 42%;
}
.wrapper {
    padding: 0 10%;
  }
  
  .header {
    height: 36px;
    background-color: #fff;
    padding: 10px;
  }
  
  .header__navbar{
    margin-left: 57%;
  }
  
  .header__image img{
    width: 40px;
    height: auto;
    margin-bottom: 3px;
  }
  
  .header__list-item,
  .header__list-item a {
    display: inline;
    margin: 0 5px;
    font-weight: 700;
    color: black;
    font-size: 1em;
    text-decoration: none;
    transition: -webkit-transform 2s;
    transition: transform 2s;
    transition: transform 2s, -webkit-transform 2s;
  }
  
  .header__list-item{
    border-left: solid 1px rgb(0, 0, 0);
  }
  
  .header__list-item a{
    margin-left: 4%
  }
  
  .header__list-item a:hover {
    color: rgb(77, 79, 82); 
  }
  
  .flexbox {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
.main-cadastro{
  background-image: url(/static/media/background3.63c583cf.jpg);
  background-size:100%;
  position: absolute;
  width: 100%;
  height: 100%;
  }

 .main-div__cadastro{
    background-color: rgba(72, 87, 255, 0.582);
    width: 100%;
    height: 100%;
}

  .container-formulario{
    background-color: #fff;
    width: 30%;
    height: 118%;
    /* box-shadow: 3px 3px 30px 1px #333; */
    padding: 1%;
    border-radius: 3px;
    margin:auto;
    -webkit-transform:translate(0px, -70px) rotate(15deg);
            transform:translate(0px, -70px) rotate(15deg)
  }
  
  .main-div__content{
    position: absolute;
    -webkit-transform:translate(40px, 200px) rotate(-15deg);
            transform:translate(40px, 200px) rotate(-15deg)
  }

  .container-formulario h2{
    /* font-size: 20px; */
    margin-left: 55px;
    margin-top: 5px;
  }
  
  .container-formulario h3{
    font-size: 13px;
    margin-left: 12px;
    margin-top: 5px;
    color: #FF6666;
  }
  .item-formulario{
    margin: 10%;
  }
  
  .item-formulario input{
    border: #ddd 1px solid;
    border-radius: 30px;
    padding: 6px;
    height: 25px;
    width: 250px;
  }
  
  .item-formulario-btn button{
    border: none;
    border-radius: 20px;
    height: 30px;
    width: 40%;
    margin-top: 3%;
    margin-left: 40%;
    background-color: rgb(72, 87, 255);
    color: #FFF;
    cursor: pointer;
  }
